@import "sass/index";
@import "../../variables.module.scss";
$desktop-gap: 10px;
$mobile-gap: 5px;
.root {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 301px;
  background: $crash;
  @include backgroundOptions;
  box-shadow: inset 0px 0.679675px 1.35935px rgba(255, 255, 255, 0.107135);
  border-radius: 6.79675px;
  @media (max-width: 600px) {
    height: 50vw;
  }
}
.result {
  // font-family: 'VAGRoundedStd';
  // font-size: 47.5772px;
  font-size: 72px;
  font-weight: 700;
  line-height: 56px;
  text-align: center;

  color: #ffffff;

  text-shadow: 0px 0px 16.3122px rgba(255, 86, 246, 0.155431);
  transform: translateY(18%);
}
.prevValue {
  position: absolute;
  top: 14px;
  left: 23px;

  font-weight: 400;
  font-size: 8.1561px;
  line-height: 10px;

  text-align: center;

  color: #7373ff;

  display: flex;
  gap: 3px;
  height: max-content;

  align-items: center;
}
